import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/cities';

export const apiCityGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiCityPost = async (serviceUrl, city) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: city ? JSON.stringify(city) : null,
  };
  return request(url, options);
};

export const apiCityPut = async (serviceUrl, city) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: city ? JSON.stringify(city) : null,
  };
  return request(url, options);
};

export const apiCityDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
